var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"info base_width"},[_c('h3',{staticClass:"header font-weight-normal"},[_vm._v("恒大再捐1亿元 支持中国红十字会开展国际抗疫人道援助")]),_c('h5',{staticClass:"tips font-weight-normal"},[_vm._v("发布时间：2020-03-23 内容来源：恒大集团")]),_c('div',{staticClass:"content"},[_c('div',{staticClass:"box-header"},[_c('p',[_vm._v("当前国际疫情防控形势日益严峻，中国正与世界携手，共抗新冠肺炎疫情。在国家援外的统一部署下，中国红十字会已派出多支医疗专家团队，赴意大利等疫情严重的国家开展防控和救治工作。3月23日，记者从中国红十字会官网获悉，恒大已向中国红十字会捐赠1亿元人民币，设立恒大国际抗疫援助基金，支持开展国际抗疫人道援助工作。")]),_c('div',{staticClass:"hd_news_company_info"},[_c('div',{staticClass:"hd_new_company_info_img bg_cover",style:({width: '570px',height:'442px',backgroundImage: ("url(" + (require('../../assets/images/news/HD-Company-23.png')) + ")")})}),_c('h4',[_vm._v("中国红十字会官网截图")])]),_c('p',[_vm._v("据世界卫生组织官网数据，截至北京时间2020年3月22日7时，中国以外新冠肺炎累计确诊病例210644例，累计死亡病例9517例。很多国家确诊人数持续上升，出现医疗资源严重不足、防护物资严重短缺、防控经验严重不足的情况，并向中国政府请求援助。目前，中国已向意大利、伊朗、伊拉克等国派出了医疗专家团队，并向韩国、日本以及欧洲几十个国家提供了抗疫物资援助。")]),_c('p',[_vm._v("作为中国援外的主要力量，中国红十字会积极行动，联合恒大等爱心企业援助境外疫情较为严重的国家和地区。此次“恒大国际抗疫援助基金”的设立，将主要用于支持中国红十字会采购受援国急需的医疗物资、防护物资，采集恢复期血浆、购置抑制细胞因子风暴药物，以及派出国际援助医疗队的后勤保障等。")]),_c('p',[_vm._v("全国人大常委会副委员长、中国红十字会会长陈竺表示，设立恒大国际抗疫援助基金，支持新冠肺炎疫情严重的国家和地区抗击疫情，加强疫情防控国际合作，是发挥中国红十字会在国际红十字运动中的作用、积极践行构建人类命运共同体思想的重要体现。此次恒大集团助力中国红十字会开展国际人道援助行动，体现了恒大集团作为一家杰出中国企业守望相助的社会责任感和休戚与共的国际情怀。")]),_c('p',[_vm._v("全国政协常委、恒大集团董事局主席许家印表示，积极承担社会责任，是恒大始终不变的初心。新冠肺炎疫情是人类面对的共同挑战，此次恒大与中国红十字会合作成立国际援助基金，将在国家援外统一部署下，为中国红十字会派出的国际援助医疗队提供支持保障，为急需帮助的国家和地区提供人道援助，希望能为全球抗疫贡献一份力量。")]),_c('p',[_vm._v("此次新冠肺炎疫情发生以来，恒大已第一时间向武汉市捐赠2亿元和5000吨新鲜蔬菜，向中国医学科学院捐赠1亿元用于加快抗疫药物研发。同时，恒大作为总协调人，联合钟南山院士团队、哈佛大学成立中美新冠病毒肺炎科研攻坚专家组，5年提供8亿元经费支持科研攻关。")]),_c('div',{staticClass:"hd_news_company_info"},[_c('div',{staticClass:"hd_new_company_info_img bg_cover",style:({width: '666px',height:'276px',backgroundImage: ("url(" + (require('../../assets/images/news/company_info13-1.gif')) + ")")})})]),_c('HDshare')],1)]),_c('infoPage',{attrs:{"first":_vm.first,"l